<template>
  <div>
    <div class="orderinfo clear_f">
      <div class="top" id="from-city">天津市市</div>
      <div class="info-arrow float_l">&nbsp;</div>
      <!-- <div style="float:left;margin-top:2px;" id="to-city">{{ GPSList.to }}</div> -->
      <div class="top" id="to-city">北京市市</div>
      <div style="clear: both"></div>
      <!-- <div style="float:left;margin-top:2px">预计到达时间：{{ GPSList.date }} 00:00</div> -->
      <div class="top">预计到达时间：2012-12-22 00:00</div>
      <!-- <div style="float:left;margin-top:2px" id="status-info">{{ GPSList.status }}</div> -->
      <div class="top" id="status-info">签收</div>
      <div class="top">订单号：9057153385</div>
      <div class="top">物流单号：ORSH19011600038</div>
      <div class="clear"></div>
    </div>
    <div>
      <ul>
        <li v-for="(item,i) in arrButton" :key="item">
          <a @click="buttonClick(i)" :class="numA === i? active: ''" href="javascript:;">{{ item }}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      arrButton: ['在途模式', '地图模式'],
      numA: 0,
      active: {
        active: true
      }
    }
  },
  methods: {
    buttonClick (i) {
      this.numA = i
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
  .orderinfo {
    margin: auto;
    max-width: 800px;
    background-color: #fff;
    padding: 8px 10px;
    color: #666;
    font-size: 14px;
    font-weight: bold;
  }
  .orderinfo .info-arrow {
    background: url(../assets/images/arrow.png) no-repeat right;
    width: 38px;
  }
  .orderinfo>div {
    margin-right: 15px;
    line-height: 30px;
    color: #666;
    font-size: 14px;
  }
  .orderinfo>div.top {
    float:left; margin-top:2px;
  }
  ul { width:100%; display: flex; display: -webkit-flex; }
  ul>li { width: 50%; }
  ul>li>a { padding: .5rem 1rem; display: block; text-align: center; }
  ul>li>a.active { background-color: #e91e63; color: #fff; }

  ul>li {
    >a {
      border: 1px solid rgba(0, 0, 0, 0.125);
      -webkit-tap-highlight-color:transparent;
    }
  }
</style>



