import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import trace from '@/components/trace'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'trace',
      component: trace
    }
  ]
})



